import $ from "jquery";
import 'select2'

/**
 *   
 */
const select2 = () => {

  /**  */
  const lang = {
    searching: function () {
      return "...";
    },
    noResults: function () {
      return "  ";
    },

    loadingMore:function(){
      return"";
    },

    errorLoading: function () {
      return "   ";
    },

    maximumSelected: function(e){
      let str="     "+ e.maximum +" ";
      return str += (e.maximum === 1) ? 'a' : '';
    }
  }

  /**    */
  const config = {
    debug : false, //->      

    disabled: false, //->  true,    

    multiple: false, //->    

    selectOnClose: false, //->       

    selectionCssClass: 'sSelect', //->    CSS   .  : ''
    dropdownCssClass: 'sSelect-dropdown', //->    CSS   .  : ''

    theme: 'default', //->  .  : default

    minimumResultsForSearch: 10, //->   ,     .  : 0

    //   : https://select2.org/i18n
    language: {
      //          select2
      ...lang
    }
  }

  /**        */
  $.fn.select2.defaults.set("width", "100%");

  $('.js-select2').select2({
    placeholder: '', // ->   .  : null

    ...config
  })
}

export default select2;
